@font-face {
  font-family: 'awethentique';
  src: url('../font/awethentique.eot?16262836');
  src: url('../font/awethentique.eot?16262836#iefix') format('embedded-opentype'),
       url('../font/awethentique.woff2?16262836') format('woff2'),
       url('../font/awethentique.woff?16262836') format('woff'),
       url('../font/awethentique.ttf?16262836') format('truetype'),
       url('../font/awethentique.svg?16262836#awethentique') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
