.app-header {
  .app-header-wrapper {
    position: relative;
    display: flex;
    padding: 32px 32px 44px;
    gap: 32px;

    .profile-info {
      .ant-typography {
        margin: 8px 0 4px;
      }
    }
  }

  .header-bg-img {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    width: 57%;
    height: 341px;
    background-image: url('../../../assets/images/header-bg.png');
    background-repeat: no-repeat;
    background-size: cover;
  }
}
